@charset "UTF-8";
:root {
  --font-body-color: rgb(255, 255, 255);
  --font-color-second: rgb(77, 91, 206);
  --font-color-title: rgb(136, 192, 255);
  --font-color-active: rgb(119, 150, 243);
  --font-color-not-active: rgba(139, 176, 255, 0.424);
  --background-color: rgb(14, 14, 14);
  --background-second: rgb(2, 10, 16);
  --background-selected: rgba(43, 45, 65, 0.337);
  --background-terminal: rgb(22, 22, 30);
  --background-side-bar: rgb(25, 27, 39);
  --background-active-bar: rgb(30, 32, 46);
  --background-color-title: rgb(14, 14, 19);
  --background-transparent: rgba(14, 14, 19, 0.855);
  --selected-color: rgb(113, 203, 255);
  --themes-color: rgb(94, 77, 168);
  --background-thumb: rgb(22, 22, 30);
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --border-color: rgba(60, 84, 141, 0.286);
  --color-active: rgb(4, 217, 255);
  --pos: center center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: rgb(59, 134, 134);
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.box-shadow {
  box-shadow: 0 1px 10px #020101;
}

@media (min-width: 490.98px) and (max-width: 1920px) {
  .font-size-calc-h2 {
    font-size: calc(28px + 22 * (100vw - 490px) / 1430);
  }
}

.top-box {
  padding: 6rem 0;
  padding-top: calc(6rem + 64px) !important;
}
@media (min-width: 1140.99px) {
  .top-box {
    padding: 10rem 0;
    min-height: 100vh;
  }
}

.pagination, .services-slide__pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;
}
.pagination .swiper-pagination-bullet, .services-slide__pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #2a3514;
  transition: background 0.5s ease-in-out;
}
.pagination .swiper-pagination-bullet:not(:last-child), .services-slide__pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active, .services-slide__pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
  transition: scale 0.5s ease-in-out;
}
.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.gradient-black {
  background-image: linear-gradient(-45deg, #1b0d39, #020101 70%);
}

.gradient-neon-revers {
  background: radial-gradient(at 95% 7%, rgba(78, 36, 75, 0.25) 0px, transparent 50%), radial-gradient(at 7% 95%, rgba(78, 36, 75, 0.25) 0px, transparent 50%);
}

.gradient-neon-color {
  background: radial-gradient(at 9% 9%, rgba(56, 31, 71, 0.35) 0px, transparent 50%), radial-gradient(at 90% 90%, rgba(56, 31, 71, 0.35) 0px, transparent 50%);
}

.gradient-border-before::before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-image: linear-gradient(to left, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-after::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  z-index: 1;
  background-image: linear-gradient(to right, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-spec {
  background-image: linear-gradient(-45deg, rgb(23, 14, 49) 0%, rgba(254, 89, 194, 0.3), rgb(23, 14, 49) 83%);
  background-size: 200% auto;
  transition: background-position 0.4s ease-in-out;
}
.gradient-spec:hover {
  background-position: right center;
}

.gradient-spec-comfort {
  background-image: linear-gradient(-45deg, #170e31 0%, rgba(200, 255, 0, 0.3), #170e31 83%);
  background-size: 200% auto;
  transition: background-position 0.4s ease-in-out;
}
.gradient-spec-comfort:hover {
  background-position: right center;
}

.liner-gradient-box {
  background-image: linear-gradient(45deg, #10133a 55%, rgba(71, 32, 138, 0.4509803922) 10%, #10133a)/cover no-repeat;
}

.text-gradient {
  background: currentColor 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient-header {
  background-image: linear-gradient(-45deg, rgb(26, 21, 48), rgb(51, 39, 100) 65%, rgb(2, 1, 1) 75%);
}

.gradient-services {
  background-image: linear-gradient(to right, #050202 20%, #1c153f 50%, #050202 80%);
}

.gradient-title {
  background-image: linear-gradient(to top, #170e31 10%, #402e6b 50%, #1a1530 100%);
}

.gradient-spec-active {
  background-image: linear-gradient(-45deg, #04d9ff, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-border-right {
  background-image: linear-gradient(to right, rgb(2, 1, 1) 20%, #332764 50%, #2a3514 80%);
}

.icon-style {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  border: 1px var(--border-color);
  border-style: none solid;
}

.shadow-after {
  z-index: -1;
  transform: rotateX(15deg) translateZ(-10px);
  color: rgba(23, 14, 49, 0.479);
  text-shadow: 1px 0 1px rgb(26, 21, 48), 0 0 3px rgba(125, 248, 255, 0.26), 0 0 10px rgba(204, 255, 0, 0.267);
}

.overflow-hidden {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}
.overflow-hidden::-webkit-scrollbar {
  height: 0;
}

.border-present {
  position: absolute;
  display: block;
  color: rgb(4, 217, 255);
  background-image: linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%);
  background-repeat: no-repeat;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  background-size: 100% 1px, 100% 1px, 1px 100%, 1px 100%;
  inset: 0.7rem;
}

.link_underline::before, .link_underline::after {
  position: absolute;
  width: 0;
  height: 1px;
  content: "";
  transition: width 0.4s ease-in-out;
  opacity: 0.6;
  background-color: rgb(4, 217, 255);
}
.link_underline::before {
  right: 0;
  bottom: 0;
}
.link_underline::after {
  bottom: 0;
  left: 0;
}
.link_underline._active::before {
  width: 45%;
}
.link_underline._active::after {
  width: 45%;
}
.link_underline:hover {
  transition: color 0.4s ease-in-out;
  color: rgb(4, 217, 255);
}
.link_underline:hover::before {
  width: 45%;
}
.link_underline:hover::after {
  width: 45%;
}
.link_underline:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.gradient-border-bottom {
  background-image: linear-gradient(to left top, rgba(129, 100, 255, 0.61) 45%, rgb(2, 1, 1) 71%);
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Manrope", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 12px solid #e7e9eb;
  position: absolute;
  right: -12px;
  top: 0;
  z-index: 1;
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "Manrope", sans-serif;
  height: 100%;
  counter-reset: heading;
  letter-spacing: 1.1px;
  color: var(--font-body-color);
}
@media screen and (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  text-shadow: 0 0 15px #000, 0 0 1em #000, 0 0 0.2em #00f;
  font: 1.6rem/1.6 "Manrope", sans-serif;
  background: rgb(2, 1, 1);
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (max-width: 490px) {
  body {
    font: 1.5rem/1.5 "Manrope", sans-serif;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  scrollbar-width: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
  user-select: none;
  pointer-events: none;
  height: auto;
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

