.services {
  height: 100%;
}
.services__body {
  height: 100%;
  position: relative;
  z-index: 9;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 6rem 0;
}
@media (min-width: 1140.99px) {
  .services__body {
    padding: 10rem 0;
  }
}
.services__title {
  margin-bottom: 6rem;
  font-family: "AA Neon", Helvetica, sans-serif;
  font-size: 60px;
  font-weight: normal;
  line-height: 100%;
  position: relative;
  z-index: 9;
  display: inline;
  text-align: center;
  letter-spacing: 0;
  color: rgb(4, 217, 255);
  filter: drop-shadow(1px 0px 3px rgb(32, 25, 71)) invert(5%);
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .services__title {
    font-size: calc(27px + 33 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .services__title {
    font-size: 27px;
  }
}
.services__content {
  width: 100%;
}

.services-slide .slide-text {
  justify-content: space-evenly;
}
.services-slide__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2.5rem;
}
.services-slide__column {
  min-width: 130px;
  border-radius: 10px;
}
.services-slide__content {
  position: relative;
  border-radius: 10px;
  box-shadow: 0 0 20px #000;
}
.services-slide__content::before {
  position: absolute;
  z-index: -1;
  top: -10px;
  right: 30px;
  bottom: -10px;
  left: 30px;
  display: block;
  content: "";
  transition: all 0.3s ease-in-out;
  opacity: 0.3;
  border-radius: 10px;
  box-shadow: 0px 0px 2px 3px rgba(4, 217, 255, 0.699);
}
@media screen and (max-width: 1024px) {
  .services-slide__content::before {
    top: 1px;
    right: 1px;
    bottom: 1px;
    left: 1px;
  }
}
@media (min-width: 1024.99px) {
  .services-slide__content:hover::before {
    top: -3px;
    right: -3px;
    bottom: -3px;
    left: -3px;
    opacity: 0.8;
  }
  .services-slide__content:hover .services-slide__img {
    position: relative;
    scale: 1.1;
  }
  .services-slide__content:hover .services-slide__bg-img {
    transform: translateY(0);
    opacity: 1;
  }
  .services-slide__content:hover .services-slide__text {
    transform: translateY(-50px);
  }
}
.services-slide__img {
  position: relative;
  z-index: -1;
}
.services-slide__image {
  overflow: hidden;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  border-radius: 10px;
}
.services-slide__image img {
  max-height: 300px;
  width: 100%;
  height: 100%;
  transition: scale 0.4s ease-in-out;
  object-fit: cover;
  object-position: center;
}
.services-slide__image svg {
  pointer-events: none;
}
.services-slide__bg-img {
  position: absolute;
  pointer-events: none;
  z-index: 2;
  top: 0;
  left: 0;
  transition: opacity 1.5s, box-shadow 0.3s ease-in-out 0.2s;
  opacity: 0;
}
.services-slide__svg {
  width: 100%;
  object-fit: cover;
}
.services-slide__svg img {
  max-width: 100%;
}
.services-slide__svg svg {
  position: absolute;
  right: 0;
  max-width: 195px;
}
.services-slide__svg svg path {
  opacity: 0;
  stroke: rgb(204, 255, 0);
}
.services-slide__text {
  position: absolute;
  pointer-events: none;
  height: 52px;
  left: 0;
  right: 0;
  bottom: 1rem;
  display: flex;
  color: rgb(4, 217, 255);
  align-items: center;
  justify-content: center;
  transition: transform 0.4s ease-in-out;
  text-align: center;
  background-color: rgba(11, 8, 19, 0.9);
}
.services-slide__text p {
  font-size: 12px;
  font-weight: normal;
}
.services-slide__text p span {
  display: block;
}
.services-slide__text p:nth-child(1) {
  color: rgb(4, 217, 255);
  text-transform: uppercase;
}
.services-slide__text p:nth-child(2) {
  color: rgb(200, 255, 0);
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .services-slide__text p {
    font-size: calc(10px + 6 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .services-slide__text p {
    font-size: 10px;
  }
}
.services-slide__text svg {
  height: 32px;
}
.services-slide__text svg path {
  fill: url(#grad);
  stroke: currentColor;
  stroke-width: 1px;
}
@media (min-width: 1024.99px) {
  .services-slide__pagination {
    display: none;
  }
}